.u-tabs {
  display: block;
  width: 100%; }

.u-tabs__tab-bar {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-content: space-between;
  align-items: flex-start;
  height: 48px;
  padding: 0 0 0 0;
  margin: 0;
  border-bottom: 1px solid rgb(224,224,224); }

.u-tabs__tab {
  margin: 0;
  border: none;
  padding: 0 24px 0 24px;
  float: left;
  position: relative;
  display: block;
  text-decoration: none;
  height: 48px;
  line-height: 48px;
  text-align: center;
  font-weight: 500;
  font-size: 14px;
  text-transform: uppercase;
  color: rgba(0,0,0, 0.54);
  overflow: hidden; }
  .u-tabs__tab:hover:after {
    border-bottom-color: #bdbdbd; }
  .u-tabs.is-upgraded .u-tabs__tab.is-active {
    color: rgba(0,0,0, 0.87); }
  .u-tabs.is-upgraded .u-tabs__tab.is-active:after {
    height: 2px;
    width: 100%;
    display: block;
    content: " ";
    bottom: 0px;
    left: 0px;
    position: absolute;
    background: rgb(63,81,181);
    animation: border-expand 0.2s cubic-bezier(0.4, 0, 0.4, 1) 0.01s alternate forwards;
    transition: all 1s cubic-bezier(0.4, 0, 1, 1); }
  .u-tabs__tab .u-tabs__ripple-container {
    display: block;
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0px;
    top: 0px;
    z-index: 1;
    overflow: hidden; }
    .u-tabs__tab .u-tabs__ripple-container .u-ripple {
      background: rgb(63,81,181); }

.u-tabs-pill .u-tabs__tab:hover {
  text-decoration: none;
  background-color: #eee; }

.u-tabs.is-upgraded .u-tabs-pill .u-tabs__tab.is-active {
  background-color: rgb(63,81,181);
  color: #FFFFFF; }
  .u-tabs.is-upgraded .u-tabs-pill .u-tabs__tab.is-active:after {
    height: 0px; }

.u-tabs__panel {
  display: block; }
  .u-tabs.is-upgraded .u-tabs__panel {
    display: none; }
  .u-tabs.is-upgraded .u-tabs__panel.is-active {
    display: block; }

@keyframes border-expand {
  0% {
    opacity: 0;
    width: 0; }
  100% {
    opacity: 1;
    width: 100%; } }

.u-tabs__tab-bar-left {
  height: 150px; }
  .u-tabs__tab-bar-left div {
    float: left; }
  .u-tabs__tab-bar-left .u-tabs__panel {
    margin-left: 30px; }
  .u-tabs__tab-bar-left > :first-child {
    margin-left: 0;
    width: 80px;
    height: 100%;
    display: inline-block !important; }
  .u-tabs__tab-bar-left a {
    width: 80px;
    float: left;
    display: block; }
  .u-tabs__tab-bar-left .u-tabs__tab-bar {
    border: none !important; }
  .u-tabs__tab-bar-left.is-upgraded .u-tabs__tab.is-active:after {
    height: 100%;
    width: 2px;
    left: 98%; }

.animation-slide-left {
